#modal {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
}

#modal-background {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
}

#modal-content {
    width: 484px;
    background-color: rgb(255, 255, 255);
    border-radius: 32px;
    position: relative;
    text-align: center;
    margin: auto;
    min-height: initial;
    box-shadow: rgba(0, 0, 0, 0.45) 0px 2px 10px;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    
    /* position: absolute;
    background-color: white;
    
    width: 484px;
    min-height: 400px;
    border-radius: 25px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    animation: fadeIn 2s; */
    /* transition: top 0.4s */
}

#modal-window{
    min-height: 400px;
    padding: 20px 10px 24px
    
    
}


@keyframes mymove {
    from {
        top: -100%;
    }

    to {
        top: 20%;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0%;
    }

    to {
        opacity: 100%;
    }
}


#m-close-button {
    display: flex;
    cursor: pointer;
    position: absolute;
    right: 0;
    height: 32px;
    width: 32px;
    margin-right: 16px;
    margin-left: 16px;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    background-color: white;
    /* margin-top: 16px; */
}

#m-close-button:hover {
    
    background-color: var(--hovelightGray);
    
}

#close-icon {
    height:24px;
    width:24px;
}

.logo-container {
    height: 45px;
    width: 45px;
    display:block;
    margin: 8px auto 6px;
    text-align: center;
}

.mlogo {
    height: 40px;
    width: 40px;
}

.header-container {
    margin: 0px auto 22px;
    width: 400px;
}

#welcome-text {
    color: rgb(51, 51, 51);
    font-size: 32px;
    font-weight: 600;
    letter-spacing: -1.2px;
    padding-left: 16px;
    padding-right: 16px;
    -webkit-font-smoothing: antialiased;
    word-break: keep-all;
}

#modal-children {
    width: 268px;
    margin: 0px auto;
}


