.input-label {
    display: block;
    margin-left: 8px;
    margin-bottom: 4px;
    text-align: left;
    word-wrap: break-word;
    font-weight: 400;
    font-size: 14px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: var(--textDefault);
}

.inputbox-container{
    position: relative;
    margin: 8px 0px;
}

.inputbox{
    border-color: var(--grayContainer);
    min-height: 48px;
    padding:8px 16px;
    box-sizing: border-box;
    width: 268px;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 16px;
    -webkit-font-smoothing: antialiased;
    appearance: none;
    border-radius: 16px;
    border-style: solid;
    border-width: 2px;
    color: var(--textDefault);
    background-color: var(--backgrounGray);

} 

.modal-button{
        border: 0px;
        height: 40px;
        display: inline-block;
        border-radius: 20px;
        -webkit-font-smoothing: antialiased;
        padding: 0px 18px;
        font-size: 15px;
        font-weight: bold;
        cursor: pointer;
        margin-top: 10px;
        margin-bottom: 10px;
        vertical-align: middle;
        text-align: center;
        color: rgb(255, 255, 255);
        width: 100%;
}
#modal-button-login{
   background-color: rgb(230, 0, 35); 
}
#modal-button-login:hover{
    background-color: var(--hoveRed);
}
#modal-button-demo{
    background-color: #0074e8;
}
#modal-button-demo:hover {
    background-color: #023d79;
}

#modal-or{
    font-weight: bold;
    font-size: 14px;
}

.warning-text{
    margin: 10px auto;
    font-size: 11px;
    color: #767676;

}

.divide-line{
    border-bottom: 1px solid #dedede;
    margin: 10px auto;
    width: 110px;
}



#io-warning{
    width: 16px;
    height: 16px;
    margin-right: 4px;
}

.modal-error-text{
    display: flex;
    text-align: left;
    width: 100%;
    align-items: center;
    margin: 8px;
    font-size: 12px !important;
    font-weight: 100;
    color: var(--textError);
}